.mobile {
  background-color: white;
  height: 100vh;
  width: 60vh;
  position: absolute;
  right: 0px;
  top: -6px;
  padding: 18px 12px;
  display: flex;
  flex-direction: column;
}

.close-icon {
  color: black;
  display: flex;
  justify-content: flex-end;
  font-size: 24px;
}

.mobile-options {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 24px;
  justify-content: 24px;
  align-items: center;
}

.mobile-option {
  font-size: 24px;
  padding: 24px 0px;
  display: flex;
}

.mobile-option a {
  text-decoration: none;
  color: black;
  display: flex;
  font: arial;
  flex-direction: rowS;
}

@media only screen and (max-width: 720px) {
  .mobile {
    height: 70vh;
    width: 40vh;
    position: absolute;
    right: 0px;
    top: -6px;
    padding: 12px 9px;
    display: flex;
    flex-direction: column;
  }
  .close-icon {
    color: black;
    display: flex;
    justify-content: flex-end;
    font-size: 20px;
  }

  .mobile-options {
    display: flex;
    flex-direction: column;
    width: 70%;
    padding-top: 24px;
    justify-content: 24px;
    align-items: center;
  }

  .mobile-option {
    font-size: 20px;
    padding: 24px 0px;
    display: flex;
  }

  .mobile-option a {
    text-decoration: none;
    color: black;
    display: flex;
    font: arial;
    flex-direction: row;
  }
}
